<script>
import CourseDataService from '../service/CourseDataService';
export default {
  name: "courseDetails",
  
  data() {
    return {
      course:{
        id:'',description:''
      },
      INSTRUCTOR: "in28minutes",
      errors: []
    };
  },
  computed: {
    id() {
      return this.$route.params.id;
    }
  },
  methods: {
    refreshCourseDetails() {
        CourseDataService.retrieveCourse(this.INSTRUCTOR, this.id).then(res => {
          this.description = res.data;
        });
    },
  },
  created() {
     console.log("in course component" );
    
    //if(editMode){
      this.refreshCourseDetails();
   // }
  },
  validateAndSubmit(e) {
        e.preventDefault();
        this.errors = [];
        if(!this.description) {
            this.errors.push("Enter valid values");
        } else if(this.description.length < 5) {
            this.errors.push("Enter atleast 5 characters in Description");
        }
        if(this.errors.length === 0) {
            //if(!editMode){
                CourseDataService.createCourse(this.INSTRUCTOR, {
                    description: this.description
                })
                .then(() => {
                    this.$router.go('/allCourses');
                });
            //}else{
               CourseDataService.updateCourse(this.INSTRUCTOR, this.id, {
